import { useNavigate } from 'react-router-dom';

const ButtonElement = () => {
    const navigate = useNavigate();

    const goToEditPanel = e => {
        navigate(
            window.baseUrl +
                '/app/edit' +
                '?func=ll&objId=' +
                window.currentWebreportId +
                '&objAction=RunReport',
            { state: { event: e, name: 'Javid' } }
        );
    };
    return (
        <button className="item-edit" onClick={e => goToEditPanel()}>
            <i className="fas fa-pen"></i>
        </button>
    );
};

export default ButtonElement;
