import React from 'react';
import ButtonElement from './ButtonElement';
import MainElement from './DataCell';

const MainContent = props => {
    return (
        <React.Fragment>
            <MainElement id="0" element={props.data.subject}>
                <ButtonElement />
            </MainElement>
            <MainElement id="1" element={props.data.applier} />
            <MainElement id="2" element={props.data.apply_date} />
            <MainElement id="3" element={props.data.protocol_date} />
            <MainElement id="4" element={props.data.presented_file} />
            <MainElement id="5" element={props.data.additional_action} />
            <MainElement id="6" element={props.data.status} />
            <MainElement id="7" element={props.data.protocol_number} />
            <MainElement id="8" element={props.data.submit_date} />
            <MainElement id="9" element={props.data.protocol_date} />
            <MainElement id="10" element="Qeyd" />
            <MainElement id="11" element="" />
            <MainElement id="12" element="" />
            <MainElement id="13" element="" />
            <MainElement id="14" element="" />
            <MainElement id="15" element="" />
        </React.Fragment>
    );
};

export default MainContent;
