import create from 'zustand';
import {devtools} from 'zustand/middleware';
import axios from 'axios';
import _ from 'lodash';
import { v4 as uuidv4 } from 'uuid';

const handleInsertCharter = async (set, get, id, charter) => {
    const formData = new FormData();
    formData.append('func', 'll');
    formData.append('objId', window.wrURLs.insert_charter);
    formData.append('objAction', 'RunReport');
    if(id){
    	formData.append('nzid', id);
    }
    if(charter){
    	formData.append('nizamname', charter);
    }
    formData.append('nexturl', '[LL_REPTAG_NEXTURL /]');
    const result = await axios({
        method: 'POST',
        url: 'https://ecm.socar.az/otcs/llisapi.dll',
        data: formData,
        headers: {
            'Content-Type': 'multipart/form-data'
        }
    });
}

const handleInsertAppeal = async (set, get, appeal) => {
    const formData = new FormData();
    formData.append('func', 'll');
    formData.append('objId', window.wrURLs.insert_appeal);
    formData.append('objAction', 'RunReport');
    if(appeal.aid){
    	formData.append('aid', appeal.aid);
    }
    if(appeal.nzid){
    	formData.append('nzid', appeal.nzid);
    }
    if(appeal.whoapplied){
	    formData.append('whoapplied', appeal.whoapplied);
    }
    if(appeal.tosecretary){
    	formData.append('tosecretary', appeal.tosecretary);
    }
    if(appeal.attch){
	    formData.append('attchlen', appeal.attch);
    	for (let i = 0; i < appeal.attch.length; i++) {
		    formData.append('attch'+(i+1), appeal.attch[i]);
	    }
    }
    if(appeal.additionalcommn){
	    formData.append('additionalcommn', appeal.additionalcommn);
    }
    if(appeal.status){
	    formData.append('status', appeal.status);
    }
    if(appeal.protocol){
    	formData.append('protocol', appeal.protocol);
    }
    if(appeal.approvedate){
    	formData.append('approvedata', appeal.approvedate);
    }
    if(appeal.protocoldate){
    	formData.append('protocoldate', appeal.protocoldate);
    }
    if(appeal.note1){
    	formData.append('note1', appeal.note1);
    }
    if(appeal.tasksbyboard){
    	formData.append('tasksbyboard', appeal.tasksbyboard);
    }
    if(appeal.stepsbykim){
    	formData.append('stepsbykim', appeal.stepsbykim);
    }
    if(appeal.taskstatus){
    	formData.append('taskstatus', appeal.taskstatus);
    }
    if(appeal.note2){
    	formData.append('note2', appeal.note2);
    }
    if(appeal.notebyaih){
    	formData.append('notebyaih', appeal.notebyaih);
    }
    if(appeal.enterdate){
	    formData.append('enterdate', appeal.enterdate);
    }
    if(appeal.performerid){
    	formData.append('performerid', appeal.performerid);
    }
        formData.append('nexturl', '[LL_REPTAG_NEXTURL /]');
    const result = await axios({
        method: 'POST',
        url: 'https://ecm.socar.az/otcs/llisapi.dll',
        data: formData,
        headers: {
            'Content-Type': 'multipart/form-data'
        }
    });
}

