import data from '../../../MOCK_DATA.json';
import React, { useState } from 'react';
import TableComponent from './TableComponent';
import { useNavigate } from 'react-router-dom';
import useStore  from '../../../stores/useStore';
import shallow from 'zustand/shallow';

const getState = state => [
    state.changeState
]

const Table = () => {
    const [showAccordion, setShowAccordion] = useState(true);
    const [filteredData, setFilteredData] = useState(data);
    const [ changeState ] = useStore(getState, shallow);
    const navigate = useNavigate();

    const tableColumns = [
        { id: 0, name: 'Mövzu' },
        { id: 1, name: 'Müraciət edən' },
        { id: 2, name: 'Müraciət tarixi' },
        { id: 3, name: 'Müraciətin Korporativ Katibə təqdim edilməsi' },
        { id: 4, name: 'Təqdim edilmiş sənədlər' },
        { id: 5, name: 'Əlavə kommunikasiya tədbirləri' },
        { id: 6, name: 'MŞ-nın qərarının statusu' },
        { id: 7, name: 'Protokol №' },
        { id: 8, name: 'Təsdiq tarixi' },
        { id: 9, name: 'Protokol daxil olub' },
        { id: 10, name: 'Qeyd' },
        { id: 11, name: 'MŞ-nın qərarından irəli gələn tapşırıqlar' },
        {
            id: 12,
            name: 'Tapşırıqların icrası istiqamətində KİM tərəfindən atılan addımlar',
        },
        {
            id: 13,
            name: 'MŞ-nın qərarından irəli gələn tapşırıqların icra statusu',
        },
        { id: 14, name: 'Qeyd 2' },
        { id: 15, name: 'Qeyd 3' },
    ];

    const checkData = e => {
        let updatedData = [];

        data.map(data_node => {
            if (parseInt(e.target.id) === 1) {
                updatedData = data.filter(el =>
                    el.applier.toLowerCase().includes(e.target.value)
                );
                setFilteredData(updatedData);
            } else if (parseInt(e.target.id) === 2) {
                updatedData = data.filter(el =>
                    el.apply_date.includes(e.target.value)
                );
                setFilteredData(updatedData);
            } else if (parseInt(e.target.id) === 3) {
                updatedData = data.filter(el =>
                    el.presented_date.includes(e.target.value)
                );
                setFilteredData(updatedData);
            } else if (parseInt(e.target.id) === 4) {
                updatedData = data.filter(el =>
                    el.presented_file.includes(e.target.value)
                );
                setFilteredData(updatedData);
            } else if (parseInt(e.target.id) === 5) {
                updatedData = data.filter(el =>
                    el.additional_action.toLowerCase().includes(e.target.value)
                );
                setFilteredData(updatedData);
            } else if (parseInt(e.target.id) === 6) {
                updatedData = data.filter(el =>
                    el.status.toLowerCase().includes(e.target.value)
                );
                setFilteredData(updatedData);
            } else if (parseInt(e.target.id) === 7) {
                updatedData = data.filter(el =>
                    el.protocol_number.includes(e.target.value)
                );
                setFilteredData(updatedData);
            } else if (parseInt(e.target.id) === 8) {
                updatedData = data.filter(el =>
                    el.submit_date.includes(e.target.value)
                );
                setFilteredData(updatedData);
            } else if (parseInt(e.target.id) === 9) {
                updatedData = data.filter(el =>
                    el.protocol_date.includes(e.target.value)
                );
                setFilteredData(updatedData);
            } else {
                updatedData = data.filter(el =>
                    el.subject.toLowerCase().includes(e.target.value)
                );
                setFilteredData(updatedData);
            }
        });
    };

    const addNew = () => {
        changeState("07ced992-9308-40f2-b736-2b2dae1826c8")
        navigate(window.baseUrl + '/app/add' + '?func=ll&objId=' + window.currentWebreportId + '&objAction=RunReport');
    }
    
    const args = {
        showAccordion,
        setShowAccordion,
        filteredData,
        setFilteredData,
        tableColumns,
        checkData,
        addNew
    }

    return (
        <TableComponent { ...args } />
    );
};

export default Table;
