const EditComponent = props => {
    return  <div id="body">
        <main className="content">
            <form>
                <div className="form-group">
                    <div className="form-group col-md-6">
                        <label htmlFor="inputState">State</label>
                        <select 
                            id="inputState" 
                            className="form-control" 
                            defaultValue={props.selectedState}
                            onChange={(e) => props.changeState(e.target.value)}>
                            <option value="none" disabled>Choose...</option>
                            {
                                props.states.map(x => {
                                    return <option 
                                            key={x.id} 
                                            value={x.id}> {x.name} </option>
                                })
                            }
                        </select>
                    </div>
                </div>
                <div className="form-group">
                    <label htmlFor="subject">Mövzu</label>
                    <input
                        type="text"
                        className="form-control"
                        id="subject"
                        value={props.subject}
                        onChange={e => props.changeSubject(e.target.value)}
                    />
                </div>
                <div className="form-row row">
                    <div className="form-group col-4">
                        <label htmlFor="inputEmail4">Müraciət edən </label>
                        <input
                            type="text"
                            className="form-control"
                            id="applier"
                            placeholder="Müraciət edən"
                            value = {props.whoApplied}
                            onChange = {e => props.changeWhoApplied(e.target.value)}
                        />
                    </div>
                    <div className="form-group col-4">
                        <label htmlFor="apply_date">Müraciət tarixi</label>
                        <input
                            type="date"
                            className="form-control"
                            id="apply_date"
                            value={props.applyDate}
                            onChange={e => props.changeApplyDate(e.target.value)}
                        />
                    </div>
                    <div className="form-group col-4">
                        <label htmlFor="presented_file">
                            Təqdim edilmiş sənədlər
                        </label>
                        <input
                            type="file"
                            className="form-control"
                            id="presented_file"
                            multiple
                            onChange={e=>props.changeFiles(e.target.files)}
                        />
                    </div>
                </div>

                <div className="form-row row">
                    <div className="form-group col-md-4">
                        <label htmlFor="presented_date">
                            Müraciətin Korporativ Katibə təqdim edilməsi
                        </label>
                        <input
                            type="date"
                            className="form-control"
                            id="presented_date"
                            value = {props.toSecretary}
                            onChange = {e => props.changeToSecretary(e.target.value)}
                        />
                    </div>
                    <div className="form-group col-md-8">
                        <label htmlFor="additional_actions">
                            Əlavə kommunikasiya tədbirləri
                        </label>
                        <textarea
                            className="form-control"
                            id="additional_actions"
                            value={props.additionalCommn}
                            onChange={e=>props.changeSetAdditionalCommn(e.target.value)}
                        ></textarea>
                    </div>
                </div>

                <div className="form-row row">
                    <div className="form-group col-3">
                        <label htmlFor="status">
                            MŞ-nın qərarının statusu
                        </label>
                        <input
                            type="text"
                            className="form-control"
                            id="status"
                            value={props.decisionStatus}
                            onChange={e => props.changeDecisionStatus(e.target.value)}
                        />
                    </div>
                    <div className="form-group col-3">
                        <label htmlFor="approve_date">Təsdiq tarixi</label>
                        <input
                            type="date"
                            className="form-control"
                            id="approve_date"
                            value={props.approveDate}
                            onChange={e=>props.changeApproveDate(e.target.value)}
                        />
                    </div>
                    <div className="form-group col-3">
                        <label htmlFor="protocol_date">
                            Protokol daxil olub
                        </label>
                        <input
                            type="date"
                            className="form-control"
                            id="protocol_date"
                            value={props.protocolDate}
                            onChange={e=>props.changeProtocolDate(e.target.value)}
                        />
                    </div>
                    <div className="form-group col-2">
                        <label htmlFor="protocol_number">Protokol №</label>
                        <input
                            type="number"
                            className="form-control"
                            id="protocol_number"
                            value={props.protocol}
                            onChange={e=>props.changeProtocol(e.target.value)}
                        />
                    </div>
                </div>

                <div className="form-row row">
                    <div className="form-group col-4">
                        <label htmlFor="note1">Qeyd</label>
                        <textarea
                            className="form-control"
                            id="note1"
                            value={props.note1}
                            onChange={e=>props.changeNote1(e.target.value)}
                        ></textarea>
                    </div>
                    <div className="form-group col-4">
                        <label htmlFor="tasks">
                            MŞ-nın qərarından irəli gələn tapşırıqlar
                        </label>
                        <textarea
                            className="form-control"
                            id="tasks"
                            value={props.tasks}
                            onChange={e => props.changeTasks(e.target.value)}
                        ></textarea>
                    </div>
                    <div className="form-group col-4">
                        <label htmlFor="steps">
                            Tapşırıqların icrası istiqamətində KİM
                            tərəfindən atılan addımlar
                        </label>
                        <textarea
                            className="form-control"
                            id="steps"
                            value={props.steps}
                            onChange={e=>props.changeSteps(e.target.value)}
                        ></textarea>
                    </div>
                </div>

                <div className="form-row row">
                    <div className="form-group col-4">
                        <label htmlFor="executionStatus">
                            MŞ-nın qərarından irəli gələn tapşırıqların icra
                            statusu
                        </label>
                        <textarea
                            className="form-control"
                            id="executionStatus"
                            value={props.executionStatus}
                            onChange={e=>props.changeExecutionStatus(e.target.value)}
                        ></textarea>
                    </div>
                    <div className="form-group col-4">
                        <label htmlFor="note2">Qeyd 2</label>
                        <textarea
                            className="form-control"
                            id="note2"
                            value={props.note2}
                            onChange={e=>props.changeNote2(e.target.value)}
                        ></textarea>
                    </div>
                    <div className="form-group col-4">
                        <label htmlFor="note3">Qeyd 3</label>
                        <textarea
                            className="form-control"
                            id="note3"
                            value={props.note3}
                            onChange={e=>props.changeNote3(e.target.value)}
                        ></textarea>
                    </div>
                </div>
                <div className="form-group row">
                    <div className="col-sm-12 button-right">
                        <button type="button" className="btn-save">
                            Save
                        </button>
                        <button type="button" className="btn-reset">
                            Reset
                        </button>
                    </div>
                </div>
            </form>
        </main>
    </div>
}

export default EditComponent;