import useStore  from '../../../stores/useStore';
import shallow from 'zustand/shallow';
import EditComponent from './EditComponent';

const getState = state => [
    state.states,
    state.subject,
    state.changeSubject,
    state.changeState,
    state.whoApplied,
    state.changeWhoApplied,
    state.applyDate,
    state.changeApplyDate,
    state.toSecretary,
    state.changeToSecretary,
    state.additionalCommn,
    state.changeSetAdditionalCommn,
    state.decisionStatus,
    state.changeDecisionStatus,
    state.approveDate,
    state.changeApproveDate,
    state.protocolDate,
    state.changeProtocolDate,
    state.protocol,
    state.changeProtocol,
    state.note1,
    state.changeNote1,
    state.tasks,
    state.changeTasks,
    state.steps,
    state.changeSteps,
    state.executionStatus,
    state.changeExecutionStatus,
    state.note2,
    state.changeNote2,
    state.note3,
    state.changeNote3,
    state.changeFiles
]

const Edit = () => {
    const [
        states, 
        subject, 
        selectedState,
        changeSubject,
        changeState,
        whoApplied,
        changeWhoApplied,
        applyDate,
        changeApplyDate,
        toSecretary,
        changeToSecretary,
        additionalCommn,
        changeSetAdditionalCommn,
        decisionStatus,
        changeDecisionStatus,
        approveDate,
        changeApproveDate,
        protocolDate,
        changeProtocolDate,
        protocol,
        changeProtocol,
        note1,
        changeNote1,
        tasks,
        changeTasks,
        steps,
        changeSteps,
        executionStatus,
        changeExecutionStatus,
        note2,
        changeNote2,
        note3,
        changeNote3,
        changeFiles
    ] = useStore(getState, shallow);
    const args = {
        states,
        subject,
        selectedState,
        changeSubject,
        changeState,
        whoApplied,
        changeWhoApplied,
        applyDate,
        changeApplyDate,
        toSecretary,
        changeToSecretary,
        additionalCommn,
        changeSetAdditionalCommn,
        decisionStatus,
        changeDecisionStatus,
        approveDate,
        changeApproveDate,
        protocolDate,
        changeProtocolDate,
        protocol,
        changeProtocol,
        note1,
        changeNote1,
        tasks,
        changeTasks,
        steps,
        changeSteps,
        executionStatus,
        changeExecutionStatus,
        note2,
        changeNote2,
        note3,
        changeNote3,
        changeFiles
    }
    return (
       <EditComponent {...args} />
    );
};

export default Edit;
