import InputLine from '../../../components/InputLine';
import HeaderLine from '../../../components/HeaderLine';
import MainContent from '../../../components/MainContent';

const TableComponent = (props) => {
    return <>
        <h1 className="App">Socar Online Platform</h1>
        {
            props.filteredData ? (
                <>
                    <div className="main-container">
                        <HeaderLine columnHeaders={props.tableColumns} />
                        <InputLine
                            columnHeaders={props.tableColumns}
                            onInput={props.checkData}
                        />
                        <div
                            className="item item-main accordion"
                            onClick={() =>
                                props.setShowAccordion(
                                    previousValue => !previousValue
                                )
                            }
                        >
                            <span>
                                1. SOCAR və aidiyyatı şəxslərlə bağlanması
                                nəzərdə tutulan və dəyəri Şirkətin aktivlərinin
                                5 (beş) faizinədək hissəsini təşkil edən
                                əqdlərin təsdiqi (SOCAR-ın nizamnaməsinin 5.5.13
                                bəndi)
                            </span>
                        </div>

                        {props.filteredData.map(el => {
                            return props.showAccordion ? (
                                <MainContent
                                    key={el.id}
                                    data={el}
                                    columnNum={props.tableColumns}
                                ></MainContent>
                            ) : null;
                        })}
                    </div>
                    <button className="btn-new" onClick={props.addNew}>Add New</button>
                </>) : (
            <div className="no-data">
                <p className="no-data--paragraph">
                    Bazada heç bir məlumat yoxdur.
                </p>
                <button className="btn-new">Add New</button>
            </div>
        )}
    </>
}

export default TableComponent;